html,
body {
  background-color: #fff;
  color: #636b6f;
  font-size: 100%;
  font-family: "Verdana", sans-serif;
  font-weight: 200;
  /*height: 100vh;*/
  margin: 0;
}

@media (max-width: 320px) {
  html {
    font-size: 75%;
  }

  .card-body {
    padding-left: 0;
    padding-right: 0;
  }
}

@media (max-width: 480px) {
  html {
    font-size: 87.5%;
  }
}

.full-height {
  height: 70vh;
}

.flex-center {
  align-items: center;
  display: flex;
  justify-content: center;
}

.position-ref {
  position: relative;
}

.top-right {
  position: absolute;
  right: 10px;
  top: 18px;
}

.content {
  text-align: center;
}

.title {
  font-size: 24px;
}

.links > a {
  color: #636b6f;
  padding: 0 25px;
  font-size: 13px;
  font-weight: 600;
  letter-spacing: 0.1rem;
  text-decoration: none;
  text-transform: uppercase;
}

.m-b-md {
  margin-bottom: 0px;
}

.btn-primary,
.btn-primary:hover,
.btn-primary:active,
.btn-primary:visited {
  background-color: #8dc73f !important;
  border-color: #8dc73f !important;
}

.btn-lg,
.btn-group-lg > .btn {
  padding: 0.375rem 0.75rem !important;
  font-size: 1rem !important;
  min-height: 3.125rem !important;
}

.input-lg {
  line-height: 2.5rem !important;
}

.text-black {
  color: black !important;
}

.custom-select-lg {
  height: 3.5rem !important;
}

.label-text {
  font-size: 0.9rem;
}

.input-lg {
  height: 3.125rem;
}

.form-control {
  margin: auto;
  height: 3.125rem !important;
}

#sidebar-toggle {
  position: absolute;
  left: -9999px;
  display: none;
}

.mobile-menu-hamburger {
  position: relative;
  display: none;
  align-items: center;
  justify-content: center;
  width: 25px;
  height: 3rem;
  cursor: pointer;
  transition: 0.4s;
  margin-left: 1rem;
  margin-bottom: 0;
}

.mobile-menu-icon {
  display: block;
  position: relative;
  background: #28a745;
  width: 90%;
  height: 2px;
  transition: 0.4s;
}

.mobile-menu-icon::after,
.mobile-menu-icon::before {
  content: "";
  display: block;
  position: absolute;
  background: #28a745;
  width: 100%;
  height: 2px;
  transition: 0.2s;
}

.mobile-menu-icon::after {
  top: 6px;
}

.mobile-menu-icon::before {
  top: -6px;
}

.mobile-menu-checkbox:checked ~ .mobile-menu-hamburger .mobile-menu-icon {
  background: transparent;
}

.mobile-menu-checkbox:checked ~ .mobile-menu-hamburger .mobile-menu-icon::before,
.mobile-menu-checkbox:checked ~ .mobile-menu-hamburger .mobile-menu-icon::after {
  top: 0;
}

.mobile-menu-checkbox:checked ~ .mobile-menu-hamburger .mobile-menu-icon::after {
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}

.mobile-menu-checkbox:checked ~ .mobile-menu-hamburger .mobile-menu-icon::before {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}

@media (max-width: 767px) {
  nav {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9999;
    background-color: #fff;
  }

  .container {
    margin-top: 3rem;
  }

  .mobile-menu-hamburger {
    display: flex;
  }

  .mobile-menu {
    position: fixed;
    display: flex;
    flex-direction: column;
    top: 3rem;
    left: -100%;
    right: 0;
    bottom: 0;
    z-index: 9999;
    width: 100%;
    height: 100vh;
    background-color: #fff;
    padding: 1.25rem;
    transition: left 0.5s;
  }

  .mobile-menu .btn {
    margin-bottom: 1rem;
  }

  .mobile-menu-checkbox:checked ~ .mobile-menu-hamburger ~ .mobile-menu {
    left: 0;
  }
}

.barcode-product-list {
  height: 90%;
  max-width: 960px;
  background-color: white;
  margin-top: 5%;
  margin-left: auto;
  margin-right: auto;
  overflow: hidden;
  padding: 1rem;
}

.barcode-product-item {
  margin-bottom: 0.5rem;
  cursor: pointer;
}

.barcode-product-item:hover {
  border: 1px solid #3490dc;
}

